import React, { useState, useRef } from 'react';
import './player.css';

import roblox from '../assets/roblox.mp3';
import siu from '../assets/siu.mp3';
import ay_mi_madre from '../assets/ay-mi-madre.mp3';
import siu2 from '../assets/siu2.mp3';
import almendra1 from '../assets/almendra1.mp3';
import almendra2 from '../assets/almendra2.mp3';
import almendra3 from '../assets/almendra3.mp3';

import cris from '../assets/imgs/cris.png';
import oof from '../assets/imgs/oof.jpg';
import almendra from '../assets/imgs/almendra.png';

export const Player = () => {

    let audios = [];
    audios.push(roblox);
    audios.push(siu);
    audios.push(ay_mi_madre);
    audios.push(siu2);
    audios.push(almendra1);
    audios.push(almendra2);
    audios.push(almendra3);

    let audiosObject = [];
    for (let i = 0; i < audios.length; i++) {
        if (i === 1) {
            let audioTemp = new Audio(audios[i]);
            audioTemp.volume = 0.2;
            audiosObject.push(audioTemp);
        } else if (i === 2 || i === 3) {
            let audioTemp = new Audio(audios[i]);
            audioTemp.volume = 0.7;
            audiosObject.push(audioTemp);
        } else {
            audiosObject.push(new Audio(audios[i]));
        }
    }

    const [isPlaying, setIsPlaying] = useState(false);

    const playAudio = (i) => {

        for (let i = 0; i < audiosObject.length; i++) {
            audiosObject[i].currentTime = 0;
        }

        if (isPlaying) {
            for (let i = 0; i < audiosObject.length; i++) {
                audiosObject[i].pause();
            }
        }

        audiosObject[i].play();

        setIsPlaying(true);
    }

    return (
        <>
            <div className="mt-5">
                <h4 className="text-center">Pulsa en los botones y ocurrirÃ¡ la magia âœ¨</h4>
            </div>
            <div className="row justify-content-center mt-3 mb-2 pb-4">
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(0)} className="green btn-circle">
                        <img src={oof} alt="oof" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(1)} className="yellow btn-circle">
                        <img src={cris} alt="cris" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(2)} className="red btn-circle">
                        <img src={cris} alt="cris" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(3)} className="blue btn-circle">
                        <img src={cris} alt="cris" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(4)} className="light-blue btn-circle">
                        <img className="almendra" src={almendra} alt="almendra" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(5)} className="fuchsia btn-circle">
                        <img className="almendra" src={almendra} alt="almendra" />
                    </button>
                </div>
                <div className="col-6 text-center p-4">
                    <button onClick={() => playAudio(6)} className="white btn-circle">
                        <img className="almendra" src={almendra} alt="almendra" />
                    </button>
                </div>
                {/* <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-warning btn-circle">El bicho 5 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-info btn-circle">El bicho 6 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-light btn-circle">El bicho 7 ðŸ˜Ž</button>
                </div>
                <div className="col-6 text-center p-3">
                    <button onClick={playAudio} className="btn btn-dark btn-circle">El bicho 8 ðŸ˜Ž</button>
                </div> */}
            </div>
            <div>
                <p className="text-end"><i>Creado por Christianinski Inc.</i></p>
            </div>
        </>
    )
}
